import axios from 'axios'
import session from '@/utils/session'
import settings from '@/settings'

export default {
    async getWordPiece({slug, offset = 0}) {
        // 获得下一个单词列表片
        const result = await axios.get(settings.apiDomain + '/api/wordbook/item/', {
          headers: {
            'X-Caoword-Token': 'testtoken',
            'Authorization': 'JWT ' + session.getUserToken()
          },
          // withCredentials: true,
          params: {
            wordbook: slug,
            offset: offset
          }
        });

        if (result.status != 200) {
            throw `API error, status=${result.status}, statusText=${result.statusText}`
        }
        return result.data;
    },

    async getBacklogNextWord() {
        // 获得下一个单词列表片
      try {
        const result = await axios.get(settings.apiDomain + '/api/wordbook/backlog/next/', {
          headers: {'X-Caoword-Token': 'testtoken',
            'Authorization': 'JWT ' + session.getUserToken()},
          // withCredentials: true,
        });

        if (result.status != 200) {
          throw `API error, status=${result.status}, statusText=${result.statusText}`
        }
        return result.data;
      } catch (err) {
          return null;
      }
    },
    async addWordToBackLog(offset) {
        // 获得下一个单词列表片

        const result = await axios({
            method: 'post',
            url: settings.apiDomain + '/api/wordbook/item/',
            headers: {'X-Caoword-Token': 'testtoken',
              'Authorization': 'JWT ' + session.getUserToken()},
            data: {
                offset
            }
        });

        if (result.status != 200) {
            throw `API error, status=${result.status}, statusText=${result.statusText}`
        }
        return result.data;
    },
    async ackReviewWord(offset) {
    // 获得下一个单词列表片

        const result = await axios({
          method: 'post',
          url: settings.apiDomain + '/api/wordbook/backlog/next/',
          headers: {'X-Caoword-Token': 'testtoken',
            'Authorization': 'JWT ' + session.getUserToken()},
          data: {
            action: 'ack',
            offset: offset
          }
        });

        if (result.status != 200) {
          throw `API error, status=${result.status}, statusText=${result.statusText}`
        }
        return result.data;
    }
}



