import userstatus from '@/api/userwordbook_status'

const state = {
  wordbookStatus: {
    slug: null,
    cursor: 0,
    name: null,
    count: 0,
    id: null
  },
  loadingStatus: {
    wordbookListData: false,
    userStatusData: false
  },
  wordbookList: [],
  mutexes: {
    userStatusLoading: false
  }
}

const actions = {
  async getUserStatus({commit, state}) {
    commit('START_LOADING_USER_STATUS');
    const data = await userstatus.getUserWordbookStatus();
    commit('USER_STATUS_READY');
    if (data.wordbook) {
      data.wordbook.cursor = data.cursor;
      commit('USER_WORDBOOK_STATUS_CHANGED', data);
      // TODO: 这里的太莫名其妙了
      commit('WORDBOOK_OFFSET_CHANGED', data.cursor - 1);
      commit('WORD_FORGOTTEN_CHANGED', data.current_backlog_count);
      commit('END_LOADING_USER_STATUS');
    }
  },
  async getWordbookList({commit, state}) {
    const data = await userstatus.getWordbookList();
    if (data) {
      commit('WORDBOOK_LIST_GOT', data);
    }
  },
  async updateUserStatus({commit, state}, slug) {
    commit('USER_WORDBOOK_STATUS_EXPIRED');
    commit('USER_WORDBOOK_EXPIRED');
    const data = await userstatus.updateUserWordbookStatus(slug);
    if (data) {
      data.wordbook.cursor = data.cursor;
      commit('USER_WORDBOOK_STATUS_CHANGED', data);
      // TODO: 这里的太莫名其妙了
      commit('WORDBOOK_OFFSET_CHANGED', data.cursor - 1);
      commit('WORD_FORGOTTEN_CHANGED', data.current_backlog_count);
      commit('USER_STATUS_READY');
    }
  }
}

const mutations = {
  START_LOADING_USER_STATUS(state) {
    state.mutexes.userStatusLoading = true;
  },
  END_LOADING_USER_STATUS(state) {
    state.mutexes.userStatusLoading = false;
  },
  WORDBOOK_LIST_GOT (state, data) {
    state.wordbookList = data;
    state.loadingStatus.wordbookListData = true;
  },
  USER_WORDBOOK_STATUS_CHANGED (state, data) {
    state.wordbookStatus.slug = data.wordbook.slug;
    state.wordbookStatus.cursor = data.wordbook.cursor;
    state.wordbookStatus.name = data.wordbook.name;
    state.wordbookStatus.count = data.wordbook.word_count;

    state.loadingStatus.userStatusData = true;

  },
  USER_WORDBOOK_STATUS_EXPIRED (state) {
    state.loadingStatus.userStatusData = false;
  }
}

const getters = {
  getCurrentWorkbookStatus(state) {
    return state.wordbookStatus;
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}



