import axios from 'axios'
import settings from '@/settings'

export default {
  async register(username, password, registerCode) {
    try {
      const result = await axios({
        url: settings.apiDomain + '/api/auth/user/register/',
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        data: {
          username: username,
          password: password,
          register_code: registerCode
        }
      });
      debugger;
    } catch (error) {
      const resp = error.response;

      throw resp;

    }
  },
  async getUserToken(username, password) {
    try {
      const result = await axios({
        url: settings.apiDomain + '/api/auth/get/',
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        data: {
          username: username,
          password: password
        }
      });
      return result.data.token;
    } catch (error) {
      const result = error.response;

      if (result.status == 400) {
        throw 'passwordError'
        }
    }
  },

  async refreshUserToken(token) {
    try {
      const result = await axios({
        url: settings.apiDomain + '/api/auth/refresh/',
        method: 'POST',
        headers: {
          'Content-type': 'application/json'
        },
        data: {
          token
        }
      });
    } catch (error) {
      const result = error.response;

      if (result.status == 400 || result.status == 401) {
        throw 'TokenError';
      }
      return result.token;
    }
  },

}


