<template>
    <div id="settings-feedback">
        <v-btn floating class="blue add-btn" @click.native="dialogVisible = !dialogVisible">
            <v-icon light>add</v-icon>
        </v-btn>

        <div>
            <v-list two-line v-if="loadingState === false">
                <v-list-item v-for="(item, index) in items" :key="item.id">
                    <v-list-tile avatar ripple @click.native="detaildialogOpen(item)">
                        <v-list-tile-content style="padding-bottom: 18px">
                            <v-list-tile-sub-title class="blue--text text--darken-2">
                                {{ item.content }}
                            </v-list-tile-sub-title>
                            <v-list-tile-sub-title v-if="item.replies.length > 0" :class="item.replies[item.replies.length - 1].is_official ? 'green--text' : 'grey--text'">
                                {{ item.replies[item.replies.length - 1].content }}
                            </v-list-tile-sub-title>
                        </v-list-tile-content>
                        <v-list-tile-action>
                            <v-list-tile-action-text class="red--text text--darken-3"
                                                     v-if="item.replies.length > 0 && item.replies[item.replies.length - 1].is_official == true">
                                有新回复
                            </v-list-tile-action-text>
                            <v-list-tile-action-text v-else/>
                            <v-list-tile-action-text class="grey--text" v-if="item.replies.length > 0">
                                {{ item.replies[item.replies.length - 1].created_time }}
                            </v-list-tile-action-text>
                        </v-list-tile-action>
                    </v-list-tile>
                    <v-divider v-if="index + 1 < items.length"></v-divider>
                </v-list-item>
            </v-list>
            <div style="margin-top: 15%; display: flex;justify-content: center;" v-else>
                <v-progress-circular indeterminate v-bind:size="70" v-bind:width="7"
                                     class="green--text"></v-progress-circular>
            </div>
            <v-dialog class="feedback-dialog-send" v-model="dialogVisible" persistent>
                <v-card>
                    <v-card-row>
                        <v-card-title>给莪反馈</v-card-title>
                    </v-card-row>
                    <v-card-row>
                        <v-card-text>
                            <v-text-field label="内容" required multi-line
                                          v-model="feedbackPostingData.feedbackContent"></v-text-field>
                        </v-card-text>
                    </v-card-row>
                    <v-card-row>
                        <v-card-text>
                            你喜欢莪吗?
                        </v-card-text>
                        <v-btn icon :class="{ likeBtn: feedbackPostingData.like, 'grey--text': !feedbackPostingData.like }"
                               @click.native="feedbackPostingData.like = !feedbackPostingData.like">
                            <v-icon>favorite</v-icon>
                        </v-btn>
                    </v-card-row>
                    <v-card-row actions>
                        <v-btn class="blue--text darken-1" flat @click.native="dialogVisible = false">关闭</v-btn>
                        <v-btn class="blue--text darken-1" flat @click.native="dialogSubmit">提交</v-btn>
                    </v-card-row>
                </v-card>
            </v-dialog>

            <v-dialog class="feedback-dialog-detail" v-model="detailOpenned" persistent scrollable>
                <v-card>
                        <v-card-title class="green darken-1 white--text">回复</v-card-title>
                    <div class="feedback-dialog-detail-content" style="overflow-y: scroll; height:300px;">
                        <v-card-row>
                                <v-card-text style="word-break: break-word; display: block !important">
                                    <span class="green--text" style="display: block">{{feedbackSelected.owner_name}}</span>
                                    {{ feedbackSelected.content }}
                                </v-card-text>
                        </v-card-row>
                        <v-card-row v-for="reply in feedbackSelected.replies" :key="reply.id">
                                <v-card-text style="word-break: break-word;">
                                    <span style="display: block" :class="reply.is_official ? 'blue--text' : 'green--text'">{{reply.owner_name}}</span>
                                    {{ reply.content }}
                                </v-card-text>
                        </v-card-row>
                    </div>
                    <v-card-row class="reply-textarea">
                        <v-card-text>
                            <v-text-field auto-grow multi-line label="回复" rows="1" v-model="feedbackPostingData.feedbackContent"></v-text-field>
                            <transition name="reply-btn">
                                <v-btn floating small primary class="close-reply-btn orange" v-if="!feedbackPostingData.feedbackContent" @click.native="detailOpenned = false; feedbackSelected = {}">
                                    <v-icon light>close</v-icon>
                                </v-btn>
                                <v-btn floating small primary class="send-reply-btn blue" v-if="feedbackPostingData.feedbackContent" @click.native="replySubmit(feedbackSelected)">
                                    <v-icon light>send</v-icon>
                                </v-btn>
                            </transition>
                        </v-card-text>
                    </v-card-row>
                </v-card>
            </v-dialog>
        </div>
    </div>
</template>
<style>
    .likeBtn {
        color: #f44336 !important;
    }
    .add-btn {
        position: absolute !important;
        z-index: 999;
        top: 30px;
        left: 90%;
    }
    .feedback-dialog-send .dialog {
        min-width: 400px;
    }
    .feedback-dialog-detail .dialog {
        min-width: 600px;
    }
    .reply-textarea .card__text {
        display: inline-flex;
        padding-bottom: 0 !important;
    }
    .feedback-dialog-detail .send-reply-btn, .feedback-dialog-detail .close-reply-btn {
        margin-left: 1.5rem;
    }
    .reply-btn-enter-active, .reply-btn-leave-active {
        transition: opacity .5s;
    }
    .reply-btn-enter, .reply-btn-leave-to {
        opacity: 0;
    }
    .feedback-dialog-detail-header {
        position: fixed;
        width: 100%;
        transform: translateZ(0);
        -webkit-transform: translateZ(0);
    }
    .feedback-dialog-detail .feedback-dialog-detail-content {
        max-height: 300px;
        padding-top: 60px;
    }
    .feedback-dialog-detail .reply-textarea {
        position: fixed;
        width: 600px;
        margin-top: -110px;
        background-color: white;
        transform: translateZ(0);
        -webkit-transform: translateZ(0);
    }
    .feedback-dialog-detail .feedback-dialog-detail-content .card__row:nth-last-child(1) {
        margin-bottom: 110px;
    }
    .feedback-dialog-detail .card {
        box-shadow: none;
    }

    @media screen and (max-width: 640px) {
        .feedback-dialog-send .dialog, .feedback-dialog-detail .dialog {
            min-width: 300px;
        }
        .feedback-dialog-detail-header, .feedback-dialog-detail .reply-textarea {
            width: 300px;
        }
    }
</style>

<script>
  import axios from 'axios'
  import session from '@/utils/session'
  import settings from '@/settings'


  export default {
    name: "settings-feedback",
    methods: {
      resetCurrentFeedbackData() {
        this.feedbackPostingData.feedbackContent = null;
        this.feedbackPostingData.like = null;
        this.feedbackPostingData.parent = null;
      },
      dialogSubmit(event) {
        this.$store.commit('FEEDBACK_DATA_LOADING');
        axios({
          method: 'post',
          url: settings.apiDomain + '/api/feedback/',
          headers: {'X-Caoword-Token': 'testtoken',
            'Authorization': 'JWT ' + session.getUserToken()},
          data: {
            content: this.feedbackPostingData.feedbackContent,
            like: this.feedbackPostingData.like,
            parent: this.feedbackPostingData.parent
          }
          // withCredentials: true,
        }).then((function () {
          this.$store.dispatch('getFeedbackData');
          this.dialogVisible = false;
          this.$store.commit('FEEDBACK_DATA_LOADED');
        }).bind(this));
      },
      detaildialogOpen(item) {
        this.detailOpenned = !this.detailOpenned;
        this.feedbackSelected = item;
      },
      replySubmit(item) {
        this.feedbackPostingData.parent = item.id;
        this.dialogSubmit();
        this.feedbackSelected = {};
        this.feedbackPostingData.feedbackContent = '';
        this.detailOpenned = false;
      }
    },
    computed: {
      loadingState () {
        return this.$store.state.feedback.status.loadingData;
      },
      items () {
        return this.$store.state.feedback.feedbackData;
      }
    },
    created () {
      this.$store.dispatch('getFeedbackData');
    },
    data() {
      return {
        dialogVisible: false,
        feedbackPostingData: {
          like: true,
          feedbackContent: '',
          parent: null
        },
        detailOpenned: false,
        feedbackSelected: {},
      }
    }
  }
</script>

<style scoped>
  @media screen and (max-width: 640px) {
      #settings-feedback button {
          left: 80%;
      }
  }
</style>



