<template>
    <v-bottom-nav fixed value=true class="white">
    <nav-button v-for="item in bottoms" :key="item.routeName"
        :name="item.name"
        :icon="item.icon"
        :link="item.link"
        :badge-str="item.badgeStr"
        :chosen="chosen"
        :routeName="item.routeName"
        v-on:bottomNavBtnChosen="chosenBtn"></nav-button>
    </v-bottom-nav>
</template>

<script>
import BottomNavBtn from '@/components/nav_button.vue'

export default {
    name: 'BottomNav',
    props: {
        bottoms: {
            type: Array,
            required: true
        }
    },
    components: {
        'nav-button': BottomNavBtn
    },
    methods: {
        chosenBtn (name) {
            this.chosen = name;
        }
    },
    data () {
        return {
            chosen: null
        }
    }
}
</script>



