import Vue from 'vue'
import Router from 'vue-router'
import FlashCard from '@/views/FlashCard.vue'
import QuickFuck from '@/views/flashcards/QuickFuck.vue'
import Settings from '@/views/settings/frame.vue'
import SettingsIndex from '@/views/settings/index.vue'
import WordbookSettings from '@/views/settings/wordbook.vue'
import Feedback from '@/views/settings/feedback.vue'
import Glimpse from '@/views/glimpse/frame.vue'
import Review from '@/views/glimpse/review.vue'

import Frame from '@/views/Frame.vue'
import Splash from '@/views/splash.vue'
import Login from '@/views/login.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/',
      component: Frame,
      children: [
        {
          path: '/',
          name: 'index',
          component: Splash
        },
        {
          path: '/glimpse',
          name: 'glimpse',
          component: Glimpse
        },
        {
          path: '/review',
          name: 'review',
          component: Review
        },
        {
          path: '/settings',
          component: Settings,
          children: [
            {
              path: '',
              name: 'settings',
              meta: {
                title: '设置'
              },
              component: SettingsIndex
            },
            {
              path: 'feedback',
              name: 'settings-feedback',
              meta: {
                title: '反馈'
              },
              component: Feedback
            },
            {
              path: 'wordbook',
              name: 'settings-wordbook',
              meta: {
                title: '词汇书选择'
              },
              component: WordbookSettings
            }
          ]
        },
        {
          path: '/wordbook',
          name: 'wordbook',
          component: FlashCard,
          children: [
            {
              path: ':wordbook/:wordId',
              component: QuickFuck
            }
          ]
        }
      ]
    }
  ]
})



