export default {
  saveUserToken(token) {
    const storage = window.localStorage;
    storage.setItem("jwtToken", token);
  },

  getUserToken() {
    const storage = window.localStorage;

    return storage.getItem("jwtToken");
  },
  clearToken() {
    const storage = window.localStorage;
    storage.setItem("jwtToken", null);
  }
}


