import axios from 'axios'
import session from '@/utils/session'
import settings from '@/settings'

export default {
  async getUserWordbookStatus() {

    try {
      const result = await axios.get(settings.apiDomain + '/api/wordbook/user_status/', {
        headers: {'X-Caoword-Token': 'testtoken',
          'Authorization': 'JWT ' + session.getUserToken()},
        // withCredentials: true,
      });
      return result.data;
    } catch (error) {
      const result = error.response;

      if (result.status == 401) {
        throw 'needLogin';
      }
      else if (result.status != 200 && result.status != 404) {
        throw `API error, status=${result.status}, statusText=${result.statusText}`;
      } else if (result.status == 404) {
        return null;
      }

    }

  },
  async getWordbookList() {
    const result = await axios.get(settings.apiDomain + '/api/wordbook/booklist/', {
      headers: {'X-Caoword-Token': 'testtoken',
        'Authorization': 'JWT ' + session.getUserToken()},
      // withCredentials: true,
    });

    if (result.status != 200 && result.status != 404) {
      throw `API error, status=${result.status}, statusText=${result.statusText}`
    } else if (result.status == 404) {
      return null;
    }
    return result.data;
  },
  async updateUserWordbookStatus(slug) {
    const result = await axios({
      method: 'post',
      url: settings.apiDomain + '/api/wordbook/user_status/',
      headers: {'X-Caoword-Token': 'testtoken',
        'Authorization': 'JWT ' + session.getUserToken()},
      data: {
        slug
      }
    });

    if (result.status != 200 && result.status != 404) {
      throw `API error, status=${result.status}, statusText=${result.statusText}`
    } else if (result.status == 404) {
      return null;
    }
    return result.data;
  }
}



