<template>

<div class="settings-container">
  <v-toolbar class="teal" light style="position: fixed">
    <v-btn icon @click.native="$router.go(-1)" v-if="$route.name !== 'settings-index'" light>
      <v-icon light>arrow_back</v-icon>
    </v-btn>
    <v-toolbar-title>{{ $route.meta.title }}</v-toolbar-title>
  </v-toolbar>
  <router-view style="padding-top: 4rem"></router-view>
</div>

</template>

<script>
import shared from '@/shared.js'

export default {
  name: "Settings",
  computed: {
    currentTitle: function () {
      let routeName = this.$route.name;
      return '设定'
    }
  },
  data () {
    return {
    }
  },
  mounted() {
    shared.eventBus.$emit('nav-changed', "settings");
  }
}
</script>



