import worditemAPI from '@/api/worditem'
import types from '@/store/mutations-type'

const state = {
  backlog: [],
  currentWord: null,
  offset: -1,
  slug: null,
  totalWords: 0,
  loadingData: false,
  currentWordbookDone: false,
  wordsRemembered: {
    value: 0
  },
  wordsForgotten: {
    value: 0
  },
  wordsLeft: {
    value: 0
  }
}

const actions = {
  setState({commit, state}, {slug}) {
    commit('USER_GLIMPSE_STATE_SET', {slug})
  },
  async getNextPiece({commit, state}, offset) {
    console.log('OFFSET', offset);
    const data = await worditemAPI.getWordPiece({
      slug: state.slug,
      offset: offset
    });
    commit(types.GOT_NEXT_WORD_PIECE, data);

    if (state.backlog.length === 0) {
      throw 'currentWordbookDone';
    }
  },

  getNextWord({commit, state, dispatch}) {
    if (state.backlog.length != 0) {
      commit(types.WORDBOOK_BACKLOG_LPOP);
    } else {
      if (state.offset == 0) {
          commit('WORDBOOK_OFFSET_CHANGED', -1);
      }
      commit(types.WORDBOOK_LOADING_DATA);
      dispatch('getNextPiece', state.offset + 1).then(() => {
        commit(types.WORDBOOK_LOADING_DATA_DONE);
        commit('RESET_CURRENT_WORDBOOK_DONE');
        dispatch('getNextWord');
      }).catch((error) => {
        // 可能背完了
        console.log('当前单词书背诵完成');
        commit('CURRENT_WORDBOOK_DONE');
      });
    }
  },

  async addForgotWord({commit, state, dispatch}, {slug, offset}) {
      const result = await worditemAPI.addWordToBackLog(offset).then((function (resp) {
          // 忘记的单词 + 1
          if (resp) {
              commit('WORD_FORGOTTEN_CHANGED', resp.current_backlog_count);
          }
      }).bind(this));

  }

}

const mutations = {
  CURRENT_WORDBOOK_DONE(state) {
    state.currentWordbookDone = true;
  },
  RESET_CURRENT_WORDBOOK_DONE(state) {
    state.currentWordbookDone = false;
  },
  USER_WORDBOOK_EXPIRED(state) {
    state.backlog = [];
    state.currentWord = null;
    state.offset = -1;
    state.wordsRemembered.value = 0;
    state.wordsForgotten.value = 0;
  },
  USER_GLIMPSE_STATE_SET (state, {slug}) {
    state.slug = slug;
  },
  APPEND_WORD_REMEMBERED (state) {
    state.wordsRemembered.value += 1;
  },
  APPEND_WORD_FORGOTTEN (state) {
    state.wordsForgotten.value += 1;
  },
  WORD_FORGOTTEN_CHANGED (state, value) {
    state.wordsForgotten.value = value;
  },
  WORDBOOK_OFFSET_CHANGED (state, value) {
    state.offset = value;
  },
  [types.WORDBOOK_LOADING_DATA] (state) {
    // 正在获取数据 flag
    state.loadingData = true;
  },
  [types.WORDBOOK_LOADING_DATA_DONE] (state) {
    // 获取数据完成 flag
    state.loadingData = false;
  },
  [types.FINISH_CURRENT_WORD_PIECE] (state) {
    console.info('当前单词片完成');
  },
  [types.WORDBOOK_BACKLOG_LPOP] (state) {
    state.currentWord = state.backlog.shift(0);
    state.offset = state.currentWord.offset;
    console.info('当前单词发生改变, 当前单词', state.currentWord, 'offset:', state.offset);
  },
  [types.GOT_NEXT_WORD_PIECE] (state, resp) {
    console.info('已获得下一个单词片');
    state.backlog = resp;
  }
}

const getters = {
  currentWord: state => state.currentWord,
}

export default {
  state,
  actions,
  mutations,
  getters
}



