<template>
    <router-link :to="link">
        <v-btn flat light :class="['bottom_nav_btn', chosen === name ? 'teal--text' : 'grey--text']" @click.native="chosenBtn()" :value="chosen === name">
            <span>{{ name }}</span>
            <v-icon v-badge="badgeStr" class="red--after unfucked_read" v-if="badgeStr || badgeStr === 0">{{ icon }}</v-icon>
            <v-icon v-else>{{ icon }}</v-icon>
        </v-btn>
    </router-link>
</template>

<script>
    import shared from '@/shared.js'
    
    export default {
        name: 'BottomNavBtn',
        beforeMount() {
            shared.eventBus.$on('nav-changed', (navName) => {
                if (this.routeName === navName) {
                    this.chosenBtn();
                }
            });
        },
        computed: {
        },
        props: {
            chosen: {
                required: true
            },
            routeName: {
                required: true
            },
            name: {
                type: String,
                required: true
            },
            icon: {
                type: String,
                required: true
            },
            link: {
                type: String,
                required: true
            },
            badgeStr: {}
        },
        template: 'template',
        methods: {
            chosenBtn() {
                this.$emit('bottomNavBtnChosen', this.name);
            }
        }
    }
</script>

<style scoped>
  @media screen and (max-width: 640px) {
    button {
        max-width: 80px !important;
        min-width: 80px !important;
    }
    .badge:after {
        right: -10px;
        font-size: 12px;
    }
  }
</style>



