const state = {
  flowControl: {
    userStatusReady: false
  },
  globalStatus: {
    loading: false,
    currentUser: {
      username: null
    }
  }
}

const mutations = {
  USER_STATUS_READY (state) {
    state.flowControl.userStatusReady = true;
  },
  loadingStatusRaise (state) {
    state.globalStatus.loading = true;
  },
  loadingStatusFall (state) {
    state.globalStatus.loading = false;
  },
  updateUser(state, {username}) {
    state.globalStatus.currentUser.username = username;
  }
}
export default {
  state,
  mutations
}



