<template>
    <div>
        <v-list two-line subheader>
            <v-subheader class="green--text text--darken-3">单词列表设定</v-subheader>
            <router-link :to="{name: 'settings-wordbook'}">
                <v-list-item>
                    <v-list-tile>
                        <v-list-tile-content>
                            <v-list-tile-title>词汇书</v-list-tile-title>
                            <v-list-tile-sub-title>一次只能选一本，重复选择会清除当前的进度<br/>（所以不要贪心。</v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list-item>
            </router-link>
        </v-list>
        <v-list two-line subheader>
            <v-subheader class="green--text text--darken-3">系统设置</v-subheader>
            <router-link :to="{name: 'settings-feedback'}">
                <v-list-item>
                    <v-list-tile>
                        <v-list-tile-content>
                            <v-list-tile-title>给我们反馈</v-list-tile-title>
                            <v-list-tile-sub-title>在享受莪的同时请赏赐我们一点你的那个…反馈吧！</v-list-tile-sub-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list-item>
            </router-link>
            <v-list-item>
                <v-list-tile @click.native="helpDialog = true">
                    <v-list-tile-content>
                        <v-list-tile-title>震动*使用指南</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list-item>

            <v-list-item>
                <v-list-tile @click.native="logout()">
                    <v-list-tile-content>
                        <v-list-tile-title class="red--text">退出当前账户 ({{ this.$store.state.global.globalStatus.currentUser.username }})</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list-item>
            <v-list-item>
                <v-list-tile>
                    <v-list-tile-content class="grey--text">
                        Caowords early alpha version<br/>
                        版本: GrowingEmbryo
                    </v-list-tile-content>
                </v-list-tile>
            </v-list-item>
        </v-list>


        <v-dialog v-model="helpDialog" persistent width="75%" scrollable>
            <v-card>
                <v-card-row>
                    <v-card-title>如何光速莪?</v-card-title>
                </v-card-row>
                <v-card-row height="60vh">
                    <v-card-text>
                        <h6 class="green--text text--darken-2">大致流程</h6>
                        <ol>
                            <li>先在 <span class="green--text text--darken-2">设置</span> 里选择一本词汇书</li>
                            <li>进入 <span class="green--text text--darken-2">单词初记</span> 部分，快速而大力的过眼熟，快速筛选自己不会的单词</li>
                            <li>单词初记完成以后，进入 <span class="green--text text--darken-2">反复记忆</span> 部分，进行360°转体翻转三周半的轮回莪状态硬肛掉所有自己不会的单词</li>
                            <li>再次选择同样的词汇书，反复上述过程，直到这本单词书被艹的体无完肤</li>
                        </ol>
                        <div style="margin-top: 10px">
                            <h6 class="green--text text--darken-2">进阶技♂巧</h6>
                            <ul>
                                <li>在背单词界面可以使用 <span class="red--text">Z</span> 键标表示<b>我记得</b>，用 <span class="red--text">/</span> 键表示<b>我不记得</b></li>
                                <li>early alpha期的东西，求你别把它玩坏了 （颤颤巍巍</li>
                                <li>目前离线数据不是很友好，在信号不好的地铁上什么的，体验可能会很糟，这个情况会在beta版得到改善。建议先在比如 <span class="green--text text--darken-2">上班时/上课时</span> 等等有WIFI的时候使用，233333</li>
                            </ul>
                        </div>
                    </v-card-text>
                </v-card-row>
                <v-card-row actions>
                    <v-btn class="green--text darken-1" flat="flat" @click.native="helpDialog = false">懂了</v-btn>
                    <!--这里加一个IM方便指♂导-->
                    <!--<v-btn class="green&#45;&#45;text darken-1" flat="flat" @click.native="dialog = false">你说这个谁懂啊！</v-btn>-->
                </v-card-row>
            </v-card>
        </v-dialog>

    </div>
</template>

<script>
  import session from '@/utils/session'
  export default {
    name: 'SettingsIndex',
    data () {
      return {
        currentTab: "index",
        helpDialog: false
      }
    },
    methods: {
      logout() {
        console.log('Logout');
        session.clearToken();
        this.$router.replace({name: 'login'});
      }
    }
  }
</script>



