import feedbackAPI from '@/api/feedback'


const state = {
  feedbackData: [], // 反馈数据
  status: {
    loadingData: false
  }
}

const mutations = {
  FEEDBACK_DATA_CHANGED (state, data) {
    state.feedbackData = data;
  },
  FEEDBACK_DATA_LOADING (state) {
    state.status.loadingData = true;
  },
  FEEDBACK_DATA_LOADED (state) {
    state.status.loadingData = false;
  }
}

const actions = {
  async getFeedbackData({state, commit}) {
    // 获取反馈数据
    commit('FEEDBACK_DATA_LOADING');
    feedbackAPI.getFeedback().then((resp) => {
      commit('FEEDBACK_DATA_CHANGED', resp);
      commit('FEEDBACK_DATA_LOADED');
    });
  }
}

export default {
  state,
  mutations,
  actions
}


