<template>
    <v-app class="login-container">
        <v-layout row justify-center>

            <v-dialog v-model="registerDialog" persistent>
                <v-card>
                    <v-card-row>
                        <v-card-title>开始莪之旅</v-card-title>
                    </v-card-row>
                    <v-alert v-bind:value="this.status.registerError.status" error>
                        {{ this.status.registerError.reason }}
                    </v-alert>
                    <v-card-row>


                        <v-card-text>
                            <v-text-field label="用户名" v-model="credential.username" required></v-text-field>
                            <v-text-field label="密码" v-model="credential.password" type="password"
                                          required></v-text-field>
                            <v-text-field label="邀请码" v-model="credential.registerCode" required></v-text-field>
                            <small>* 表示是必填项目</small>
                        </v-card-text>
                    </v-card-row>
                    <v-card-row actions>
                        <v-btn class="green--text darken-1" flat @click.native="registerDialog = false">关闭</v-btn>
                        <v-btn class="green--text darken-1" flat @click.native="registerUser()">提交</v-btn>
                    </v-card-row>
                </v-card>
            </v-dialog>

            <v-container style="max-width: 460px">
                <v-toolbar class="white login-banner" dark>
                    <v-toolbar-title>Login</v-toolbar-title>
                    <img src="static/login_banner.png"
                         style="margin-bottom: 12%; margin-right: 0px; display: flex; width: 35%;" ></img>
                </v-toolbar>
                <v-card>

                    <v-card-text>
                        <v-alert v-bind:value="this.status.passwordError" error>
                            账号密码错误
                        </v-alert>
                        <v-alert v-bind:value="this.status.registered" success>
                            账号密码错误
                        </v-alert>
                        <div class="login-input-container">
                            <v-layout row wrap>
                                <v-flex xs12>
                                    <v-text-field
                                            label="用户名"
                                            v-model="credential.username"
                                    ></v-text-field>
                                </v-flex>
                                <v-flex xs12>
                                    <v-text-field
                                            label="密码"
                                            v-model="credential.password"
                                            :append-icon="passwordHide ? 'visibility' : 'visibility_off'"
                                            :append-icon-cb="() => (passwordHide = !passwordHide)"
                                            :type="passwordHide ? 'password' : 'text'"
                                    ></v-text-field>
                                </v-flex>
                            </v-layout>
                        </div>

                    </v-card-text>
                    <v-card-row actions>
                        <v-btn flat class="green--text darken-1" @click.native="registerDialog = true">新建账号</v-btn>
                        <v-btn flat class="green--text darken-1" @click.native="getToken()">登录</v-btn>
                    </v-card-row>
                </v-card>
            </v-container>
        </v-layout>
    </v-app>

</template>

<style>
    .login-banner {
        box-shadow: 0 2px 0px -1px rgba(0, 0, 0, 0.2), 0 0px 0px rgba(0, 0, 0, 0.14), 0 1px 10px rgba(0, 0, 0, 0.12) !important;
    }

    .login-container {
        margin-top: 10%;
        display: flex;
        justify-content: center;
    }

    .login-input-container {
        margin: 10px 10px 10px 10px;
    }

    .login-title {
        text-align: center;
    }

</style>
<script>
  import api from '@/api/login'
  import session from '@/utils/session'

  export default {
    methods: {

      getToken() {
        api.getUserToken(this.credential.username, this.credential.password).then((function (token) {
          session.saveUserToken(token);
          console.log('登陆成功');
          this.$router.replace({name: 'index', replace: true});
          this.$store.commit('updateUser', {username: this.credential.username});
        }).bind(this)).catch((function (error) {
          console.log('登陆失败', error);
          this.status.passwordError = true;
        }).bind(this));
      },
      registerUser() {
        api.register(this.credential.username, this.credential.password, this.credential.registerCode).then((function (resp) {
          console.log('注册成功');
          this.status.registered = true;
          this.getToken();
          this.registerDialog = false;
        }).bind(this)).catch((function (error) {
          console.log('注册失败');
          this.status.registerError.status = true;
          if (error.status == 400) {
            if ('error' in error.data) {
              this.status.registerError.reason = error.data;
            } else {
              if ('non_field_errors' in error.data) {
                this.status.registerError.reason = error.data['non_field_errors'].join(";");
              } else {
                var reason = '';
                for (var k in error.data) {
                  reason += `${k}: ${error.data[k]}`;
                }
                this.status.registerError.reason = `以下字段有误 ${reason}`;
              }
            }
          }
          else {
            this.status.registerError.reason = `未知错误
                ${error.status};
                错误原因: ${JSON.stringify(error.data)}`
          }


        }).bind(this));

      }

    },

    data () {
      return {
        status: {
          passwordError: false,
          registered: false,
          registerError: {
            status: false,
            reason: null
          }
        },
        credential: {
          username: null,
          password: null,
          registerCode: null
        },
        passwordHide: true,
        registerDialog: false
      }
    }
  }

</script>



