import userEventAPI from '@/api/user_events'


const state = {
  data: {
    userEventsData: []
  },
  loadingStatus: {
    userEventLoading: false
  }
};

const actions = {
  getUserEvents({commit}) {
    commit('USER_EVENTS_LOADING');
    userEventAPI.getUserEvents().then((function (resp) {
      commit('USER_EVENTS_CHANGED', resp);
      commit('USER_EVENTS_LOADED');
    }).bind(this));
  }
};

const mutations = {
  USER_EVENTS_CHANGED (state, data) {
    state.data.userEventsData = data;
  },
  USER_EVENTS_LOADING (state) {
    state.loadingStatus.userEventLoading = true;
  },
  USER_EVENTS_LOADED (state) {
    state.loadingStatus.userEventLoading = false;
  }
};

export default {
  state,
  actions,
  mutations
}


