import worditemAPI from '@/api/worditem'

const state = {
  currentWord: {
    word: null,
    exp: null,
    slug: null,
    offset: null,
    wordbookName: null
  },
  loadingData: false
}

const mutations = {
  BACKLOG_NEXT_WORD_GOT (state, data) {
    //{"word": "abandon", "exp": "\u653e\u5f03;\u653e\u7eb5", "meta": {"wordbook": {"name": "GRE\u7ea2\u5b9d08", "slug": "grehong-bao-08"}, "offset": 0}}
    state.currentWord.word = data.word;
    state.currentWord.exp = data.exp;
    state.currentWord.slug = data.meta.wordbook.slug;
    state.currentWord.wordbookName = data.meta.wordbook.name;
    state.currentWord.offset = data.meta.offset;
  },
  REVIEW_CURRENTWORD_EXPIRED (state) {
    //{"word": "abandon", "exp": "\u653e\u5f03;\u653e\u7eb5", "meta": {"wordbook": {"name": "GRE\u7ea2\u5b9d08", "slug": "grehong-bao-08"}, "offset": 0}}
    state.currentWord.word = null;
    state.currentWord.exp = null;
    state.currentWord.slug = null;
    state.currentWord.wordbookName = null;
    state.currentWord.offset = null;
  },
  REVIEW_DATA_LOADING (state) {
    state.loadingData = true;
  },
  REVIEW_DATA_LOADED() {
    state.loadingData = false;
  }
}

const actions = {
  async getBacklogWord({commit, state, dispatch}) {
    commit('REVIEW_DATA_LOADING');
    const data = await worditemAPI.getBacklogNextWord();
    if (data) {
      commit('BACKLOG_NEXT_WORD_GOT', data);
    } else {
      // no more forgotten word, clear currentWord
      commit('REVIEW_CURRENTWORD_EXPIRED');
    }
    commit('REVIEW_DATA_LOADED');
  },
  async ackWordAction({commit, state, dispatch}, offset) {
    commit('REVIEW_DATA_LOADING');
    const data = await worditemAPI.ackReviewWord(offset);
    commit('WORD_FORGOTTEN_CHANGED', data['review-forgotten-count'])
    commit('REVIEW_DATA_LOADED');
  },
}

const getters = {

}


export default {
  state,
  mutations,
  actions,
  getters
}


