<template>
  <v-app v-if="this.$store.state.global.flowControl.userStatusReady || !this.$store.state.global.globalStatus.loading">
    <main>
      <transition name="fade" appear>
        <router-view></router-view>
      </transition>
      <v-card class="bottom-place-holder"></v-card>
      <bottom-nav :bottoms="bottomNavButtons"></bottom-nav>
    </main>

  </v-app>
    <v-app v-else>
        <div style="margin-top: 15%; display: flex;justify-content: center;">
            <v-progress-circular indeterminate v-bind:size="70" v-bind:width="7"
                                 class="green--text"></v-progress-circular>
        </div>
        <div style="margin-top: 20px; display: flex;justify-content: center;">
            <span class="headline green--text">CONNECTING</span>

        </div>
    </v-app>
</template>

<style>
.bottom-place-holder {
  height: 56px !important;
  box-shadow: none !important;
}

</style>
<script>
import BottomNav from '@/components/nav_bottom.vue'
import shared from '@/shared.js'
import { mapGetters } from 'vuex'


  export default {
    name: "Index",
    components: {
      'bottom-nav': BottomNav
    },
    created () {
        if (!this.$store.state.settings.loadingStatus.userStatusData) {
          this.$store.dispatch('getUserStatus').catch((function (error) {
            if (error === 'needLogin') {
              // 需要登录
//              window.location.hash = '/login';
              this.$router.replace({ name: 'login', replace: true});
            } else {
              console.log(error, '需要重试')
            }
          }).bind(this));
        }
    },
    methods: {
        toggleNavBtn(btnName, event) {
        }
    },
    computed: {
      wordsForgottenCount() {
        return this.$store.getters.getCounter.wordsForgotten
      }
    },
    data () {
      return {
        bottomNavButtons: [
          {
            name: "概览",
            icon: "dashboard",
            routeName: "index",
            link: this.$router.resolve({name: 'index'}).resolved.fullPath
          },
          {
            name: "单词初记",
            routeName: "glimpse",
            icon: "history",
            link: this.$router.resolve({name: 'glimpse'}).resolved.fullPath,
            badgeStr: this.$store.state.wordbook.wordsLeft
          },
          {
            name: "反复记忆",
            icon: "whatshot",
            routeName: "review",
            link: this.$router.resolve({name: 'review'}).resolved.fullPath,
            badgeStr: this.$store.state.wordbook.wordsForgotten
          },
          {
            name: "设置",
            routeName: "settings",
            icon: "settings",
            link: this.$router.resolve({name: 'settings'}).resolved.fullPath
          }
        ]
      }
    }
  }
</script>




