<template>
<div v-if="wordbookListDataLoaded">
<v-dialog v-model="dialogVisible" persistent style="display: inline !important;">
  <v-card>
    <v-card-row>
      <v-card-title>你确定要更换单词书吗?</v-card-title>
    </v-card-row>
    <v-card-row>
      <v-card-text>更换单词书会导致重新生成单词索引，这项操作会覆盖当前的背诵进度，并且操作是不可逆的，请再三考虑。</v-card-text>
    </v-card-row>
    <v-card-row actions>
      <v-btn class="green--text darken-1" flat="flat" @click.native="dialogVisible = false">取消</v-btn>
      <v-btn class="green--text darken-1" flat="flat" @click.native="dialogVisible = false; dialogOk();">确定</v-btn>
    </v-card-row>
  </v-card>
</v-dialog>
<v-card>
<v-divider></v-divider>
  <v-card horizontal flat>
    <v-card-column class="blue white--text">
      <v-card-row>
        <v-spacer></v-spacer>
        <v-card-text class="text-xs-left" v-if="userStatusLoaded">
          <strong>当前 Wordbook</strong>
          <div>{{ current_wordbook.name }}</div>
        </v-card-text>

        <v-card-text class="text-xs-left" v-else>
          <strong>请选选择您要艹的单词书</strong>
        </v-card-text>

      </v-card-row>
    </v-card-column>
  </v-card>
 <v-divider></v-divider>
  <v-list>
    <v-list-group v-for="item in items" v-model="item.active" v-bind:key="item.title">
      <v-list-tile slot="item">
        <v-list-tile-content>
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <v-icon>keyboard_arrow_down</v-icon>
        </v-list-tile-action>
      </v-list-tile>
      <v-list-item v-for="subItem in item.items" v-bind:key="subItem.title">
        <v-list-tile class="list-group" @click.native="dialogVisible = true; updatingWordbook = subItem.slug">
          <v-list-tile-content>
            <v-list-tile-title>
                <v-icon medium class="green--text text--darken-2 current-tick" v-if="current_wordbook.slug === subItem.slug">done</v-icon>
                <span style="margin-left: 48px">{{ subItem.title }}</span>
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list-item>
    </v-list-group>
  </v-list>
</v-card>
</div>
<div v-else></div>
</template>
<style>
.list-group {
    padding-left: 23px !important;
}

.current-tick {
    position: absolute;
    /*top: -14px*/
}
</style>
<script>
export default {
    name: 'WordbookSettings',
    methods: {
        dialogOk() {
            if (this.updatingWordbook) {
                this.$store.dispatch('updateUserStatus', this.updatingWordbook);
            }
        }
    },
    created () {
      const state = this.$store.state.settings.loadingStatus;
      if (!state.userStatusData) {
        // 更新一下数据
        this.$store.dispatch('getUserStatus');
      }
      this.$store.dispatch('getWordbookList');
    },
    computed: {
        items() {
            const wordbookData = this.$store.state.settings.wordbookList;
            /*
            {
                1: {
                    title: "GRE",
                    items: []
                }
            }
            */
            var temp = {};
            for (var i in wordbookData) {
                var e = wordbookData[i];
                if (e.category.id in temp) {
                    temp[e.category.id].items.push({
                        title: e.name,
                        slug: e.slug
                    })
                } else {
                    temp[e.category.id] = {
                        title: e.category.name,
                        items: [{
                            title: e.name,
                            slug: e.slug
                        }]
                    }
                }
            }

            let data = Object.keys(temp).map(function(key, index) {
                return temp[key];
            });

            // 不知道vue里有没有更好的实现
            data.forEach((e) => {
                let checkActive = e.items.filter((ei) => {
                    return ei.slug === this.current_wordbook.slug;
                });

                if (checkActive.length > 0) {
                    e.active = true;
                }

            });

            return data;
        },
        current_wordbook() {
            return this.$store.getters.getCurrentWorkbookStatus;
        },
        wordbookListDataLoaded () {
            return this.$store.state.settings.loadingStatus.wordbookListData;
        },
        userStatusLoaded() {
            return this.$store.state.settings.loadingStatus.userStatusData;
        }
    },
    data () {
        return {
            dialogVisible: false,
            currentTab: "wordbook-settings",
            updatingWordbook: null,
            action: { icon: 'restaurant', class: 'grey--text' },
            title: 'Dining',
        }
    }
}
</script>



